body {
  background-image: url(https://www.minecraft.net/etc.clientlibs/minecraft/clientlibs/main/resources/img/feature-content/bg-wool-light.png);
  background-image: url(background.png);
  background-repeat: repeat;
  height: 100%
}

.fade-enter{
   opacity: 0;
}
.fade-exit{
   opacity: 1;
}
.fade-enter-active{
   opacity: 1;
}
.fade-exit-active{
   opacity: 0;
}
.fade-enter-active,
.fade-exit-active{
   transition: opacity 500ms;
}


.grids {
  justify-content: center;
  display: flex;

}

#grid {
  width: 66vmin;
  padding: 2vmin;
}

.grid-element {
  background-color: #8b8b8b;
  border-top: thick double #373737;
  border-left: thick double #373737;
  border-right: double #FFFFFF;
  border-bottom: double #FFFFFF;
  float: left;
  width: 20vmin;
  height: 20vmin;

}

img.displayed {
  display: block;
  margin: auto;
  padding: 10px;
  width: 18vmin;
}





#buttons {
  text-align: right;
  padding: 1vmin;
}

#buttons > button {
 margin-right: 1vmin;
 font-size: 2vmin;
}


